import React from 'react';

function TitleBand(props) {
  const { rounded, title, position } = props;
	return(
		<div className={`container${rounded?" rounded-band":""}`}>
      
      <h2>{title}</h2>

      <style jsx>{`
        .container{
          width:100%;
          background: #111317;
          margin:40px 0px;
          margin-bottom: 25px;
        }
        .rounded-band{
          border-radius: 10px;
        }
        h2{
          margin:0;
          padding:15px;
          color:#fff;
          text-align: ${position || "left"};
        }
        @media only screen and (max-width : 768px) {
          h2{
            font-size: 18px;
            padding:10px;
          }
        }
      `}</style>
    </div>
	);
}

export default TitleBand;